.intro-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    font-family: 'Noto Sans KR', sans-serif;
  }
  
  /* ìŠ¬ë¼ì´ë” ì˜ì—­ - ì¶”í›„ í™•ì¥ */
  .imageSlider-container {
    
    width: 100%;
    height: 300px;
    background-color: #e0e0e0;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
  }
  
  /* íšŒì¥ ì¸ì‚¬ë§ ì „ì²´ ë ˆì´ì•„ì›ƒ */
  .main-container {
    display: flex;
    flex-direction: row;

    padding: 2rem;
    background-color: white;
    border-top: 4px solid #003f87;
    gap: 10rem;
  }
  
  .image-container {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    padding-bottom: 1.5rem;
    flex: 4;
  }
  
  .image-container img {
    width: 100%;
    height: auto;
    object-fit: contain;
    border-radius: 6px;
    z-index: 1;
  }
  
  /* íƒ€ì´í‹€ ë°•ìŠ¤ë¥¼ ì¢Œì¸¡ ìƒë‹¨ì— ê³ ì • */
  .title-box {
    position: absolute;
    bottom: 3rem;
    left: 0.3rem;
    background-color: #1c62b3;
    color: white;
    padding: 0.4rem 1rem;
    font-weight: bold;
    font-size: 1.5rem;
    z-index: 2;
    text-align: left;
  }
  
  /* ì´ë¦„ì€ ê·¸ëŒ€ë¡œ ìœ ì§€ */
  .name {
    background-color: white;
    color: black;
    font-weight: bold;
    font-size: 1.5rem;
    margin-top: 0.4rem;
    padding-left: 0.5rem;
    border-bottom: 3px solid #4a9cf9;
    padding-bottom: 1rem;
    width: 100%;
    text-align: right;
  }
  
  /* ì˜¤ë¥¸ìª½ í…ìŠ¤íŠ¸ */
  .text-container {
    width: 70%;
    display: flex;
    flex-direction: column;
    flex:6;
  }
  
  .h2-text1 {
    color: #003f87;
    font-weight: bold;
    font-size: 1.3rem;
    margin-bottom: 1rem;
    padding-top: 1rem;
  }
  
  .text-container p {
    font-size: 1rem;
    font-weight: bold;
    line-height: 1.8;
    margin-bottom: 1rem;
  }
  
  .text-container .quote {
    color: #005fcc;
    font-weight: bold;
    margin-top: 1rem;
  }
  
  .text-container .sign {
    font-weight: bold;
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }
  @media (max-width: 768px) {
    .main-container {
      flex-direction: column;
    }
  
    .image-container,
    .text-container {
      width: 100%;
    }
  }
  